  font-style: normal;
}

.mbsc-ic:before {
  font-family: 'icons_mobiscroll';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Icons */
.mbsc-ic-checkmark:before {
  content: "\f100";
}

.mbsc-ic-close:before {
  content: "\f101";
}

.mbsc-ic-loop2:before {
  content: "\f102";
}

.mbsc-ic-minus:before {
  content: "\f103";
}

.mbsc-ic-plus:before {
  content: "\f104";
}

.dw,
.dwo {
  -webkit-tap-highlight-color: transparent;
}

.dw {
  max-width: 98%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  font-size: 12px;
  text-shadow: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -ms-touch-action: none;
  user-select: none;
  /*touch-action: none;*/
  /* Kills native scroll in Chrome >=35 */
}

.dw:focus {
  outline-color: transparent;
}

.dw-rtl {
  direction: rtl;
}

/* Box sizing */
.dw,
.dwbc {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.dwwr {
  min-width: 170px;
  zoom: 1;
  overflow: hidden;
  text-align: center;
  font-family: arial, verdana, sans-serif;
}

/* Modal overlay */
.dw-persp,
.dwo {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.dw-persp {
  z-index: 99998;
}

.dwo {
  z-index: 1;
  background: #000;
  background: rgba(0, 0, 0, 0.7);
  filter: Alpha(Opacity=70);
}

/* Liquid mode */
.dw-liq .dw {
  max-width: 100%;
}

/* Top/Bottom mode */
.dw-top .dw,
.dw-bottom .dw {
  width: 100%;
  max-width: 100%;
}

/* Inline mode */
.dw-inline .dw {
  position: static;
  display: inline-block;
  max-width: 100%;
}

.dw-inline.dw-liq .dw-persp .dw {
  display: block;
}

.dw-inline .dw-persp {
  position: static;
}

/* Bubble mode */
.dw-bubble .dw {
  margin: 20px 0;
}

.dw-bubble .dw-arrw {
  position: absolute;
  left: 0;
  width: 100%;
}

.dw-bubble-top .dw-arrw {
  bottom: -36px;
}

.dw-bubble-bottom .dw-arrw {
  top: -36px;
}

.dw-bubble .dw-arrw-i {
  margin: 0 30px;
  position: relative;
  height: 36px;
}

.dw-bubble .dw-arr {
  display: block;
}

.dw-arr {
  display: none;
  position: absolute;
  left: 0;
  width: 0;
  height: 0;
  border-width: 18px 18px;
  border-style: solid;
  margin-left: -18px;
}

.dw-bubble-bottom .dw-arr {
  top: 0;
}

.dw-bubble-top .dw-arr {
  bottom: 0;
}

.dw-hidden {
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  border: 0;
  overflow: hidden;
}

/* Buttons */
.dwb {
  overflow: hidden;
  display: block;
  text-decoration: none;
  white-space: nowrap;
  text-overflow: ellipsis;
  vertical-align: top;
}

.dwb-e {
  cursor: pointer;
}

.dwb-d {
  cursor: default;
}

/* Button container */
.dwbc {
  display: table;
  width: 100%;
  text-align: center;
}

/* Button wrapper */
.dwbw {
  vertical-align: top;
  display: table-cell;
  position: relative;
  z-index: 5;
}

/* Default theme */
.mbsc-mobiscroll .dwwr {
  min-width: 220px;
  background: #f7f7f7;
  color: #454545;
  font-size: 16px;
}

.mbsc-mobiscroll .dwv {
  padding-top: .6666em;
  color: #4eccc4;
  font-size: .75em;
  text-transform: uppercase;
  line-height: 2em;
}

.mbsc-mobiscroll .dwbc {
  display: block;
  overflow: hidden;
  text-align: right;
  padding: 0 0.5em 0.5em 0.5em;
}

.mbsc-mobiscroll .dwbw {
  display: block;
  float: right;
}

.mbsc-mobiscroll .dw-rtl .dwbw {
  float: left;
}

.mbsc-mobiscroll .dwb {
  height: 2.5em;
  line-height: 2.5em;
  padding: 0 1em;
  color: #4eccc4;
  text-transform: uppercase;
}

.mbsc-mobiscroll .dwb-a {
  background: rgba(78, 204, 196, 0.3);
}

.mbsc-mobiscroll .dw-bubble-bottom .dw-arr {
  border-color: transparent transparent #f7f7f7 transparent;
}

.mbsc-mobiscroll .dw-bubble-top .dw-arr {
  border-color: #f7f7f7 transparent transparent transparent;
}

.dwwb,
.dwwo,
.dwwol {
  -webkit-backface-visibility: hidden;
}

/* Wheel container wrapper */
.dwc {
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
  overflow: hidden;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

/* Wheel label */
.dwl {
  line-height: 30px;
  height: 30px;
  top: -30px;
  left: 0;
  text-align: center;
  white-space: nowrap;
  position: absolute;
  width: 100%;
}

/* Wheel container */
.dwwc {
  margin: 0 auto;
  position: relative;
  zoom: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
}

.dwfl {
  max-width: 100%;
  -webkit-box-flex: 1;
  -webkit-flex: 1 auto;
  -ms-flex: 1 auto;
  flex: 1 auto;
}

/* Wheels */
.dwwl {
  position: relative;
  z-index: 5;
}

.dwww {
  position: relative;
  padding: 1px;
  /*overflow: hidden;*/
}

.dww {
  overflow: hidden;
  position: relative;
}

.dw-bf {
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000px;
  backface-visibility: hidden;
  perspective: 1000px;
}

.dw-ul {
  position: relative;
  z-index: 3;
}

.dw-li {
  padding: 0 5px;
  position: relative;
  text-align: center;
  white-space: nowrap;
  vertical-align: bottom;
  opacity: .3;
  filter: Alpha(Opacity=30);
}

/* Valid entry */
.dw-li.dw-v {
  opacity: 1;
  filter: Alpha(Opacity=100);
}

/* Hidden entry */
.dw-li.dw-h {
  visibility: hidden;
}

.dw-i {
  position: relative;
  height: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Clickpick mode */
.dwwb {
  position: absolute;
  z-index: 4;
  left: 0;
  cursor: pointer;
  width: 100%;
  text-decoration: none;
  text-align: center;
  opacity: 1;
  -webkit-transition: opacity 0.2s linear;
  transition: opacity 0.2s linear;
}

.dwa .dwwb {
  opacity: 0;
}

.dwpm .dwwbp {
  top: 0;
}

.dwpm .dwwbm {
  bottom: 0;
}

.dwpm .dwwol {
  display: none;
}

/* Wheel overlay */
.dwwo {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

/* Background line */
.dwwol {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 0;
  width: 100%;
  pointer-events: none;
}

/* Liquid mode */
.dw-liq .dwc {
  display: block;
}

.dw-liq .dw-tbl {
  width: 100%;
  table-layout: fixed;
}

/* Hidden label */
.dwhl .dwl {
  display: none;
}

/* Hidden select element */
.dw-hsel {
  position: absolute;
  height: 1px;
  width: 1px;
  left: 0;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
}

/* Multiple lines */
.dw-ml .dw-li {
  overflow: hidden;
}

.dw-ml .dw-li .dw-i {
  width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: middle;
  white-space: normal;
}

/* Multiple selection */
.dwwms .dw-li {
  padding: 0 40px;
}

.dwwms .dwwol {
  display: none;
}

.dw-msel:before {
  width: 40px;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
}

/* Select groups */
.dww .dw-w-gr {
  padding: 0 5px;
  opacity: 1;
  font-weight: bold;
  text-align: left;
}

/* Default theme */
.mbsc-mobiscroll .dwc {
  padding: 2em 0.25em 0 0.25em;
}

.mbsc-mobiscroll .dwl {
  color: #4eccc4;
  font-size: .75em;
  text-transform: uppercase;
}

.mbsc-mobiscroll .dwhl {
  padding-top: 0;
}

.mbsc-mobiscroll .dwfl {
