}

.mbsc-mobiscroll .dwwol {
  border-top: 1px solid #4eccc4;
  border-bottom: 1px solid #4eccc4;
}

/* Clickpick mode */
.mbsc-mobiscroll .dwpm .dwwol {
  display: block;
}

.mbsc-mobiscroll .dwwb {
  color: #4eccc4;
  background: #f7f7f7;
}

.mbsc-mobiscroll .dwwbp {
  bottom: 0;
  top: auto;
}

.mbsc-mobiscroll .dwwbm {
  top: 0;
  bottom: auto;
}

.mbsc-mobiscroll .dwwb span {
